.cover {
  background: black;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.cover img {
  width: auto;
  height: 100vh;
}

.cover header {
  color: #fff;
  position: absolute;
  left: 1rem;
  text-align: center;
  width: 48rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cover p {
  margin: 0;
  font-weight: bold;
  letter-spacing: 2px;
}

.cover h1 {
  font-size: 4rem;
  margin: 0;
  border-right: 4px solid;
  width: 19ch;
  white-space: nowrap;
  overflow: hidden;
  animation: typing 2s steps(19), blinking 0.5s infinite step-end alternate;
}

@keyframes typing {
  from {
    width: 0;
  }
}
@keyframes blinking {
  50% {
    border-color: transparent;
  }
}

.heroPic {
  position: absolute;
  right: 15vw;
}

.btns {
  width: 40vw;
  margin-top: 10px;
  display: flex;
  gap: 2rem;
  justify-content: center;
}
.btns button {
  outline: none;
  background-color: transparent;
  height: 3rem;
  border: 2px solid #e71d36;
  color: #e71d36;
  cursor: pointer;
  transition: 0.2s ease;
  font-weight: bold;
}

button:hover {
  background-color: #e71d36;
  color: inherit;
}

.btns a {
  color: inherit;
  text-decoration: none;
}

@media (max-width: 375px) {
  .cover img {
    position: relative;
    z-index: 0;
  }
  .cover header {
    z-index: 1;
    bottom: 0;
    left: 0;
    width: 375px;
    background: linear-gradient(360deg, black 80%, transparent);
    padding-top: 15px;
  }
  .cover h1 {
    font-size: 2.2rem;
  }

  .heroPic {
    right: 4vw;
  }

  .btns {
    gap: 1rem;
  }

  .btns button {
    width: 110px;
  }
}

@media (max-width: 500px) {
  .cover {
    align-items: end;
  }
  .cover img {
    position: relative;
    z-index: 0;
  }

  .cover header {
    position: relative;
    z-index: 1;
    bottom: 0;
    width: 375px;
    background: linear-gradient(360deg, black 80%, transparent);
    padding-top: 15px;
  }
  .cover h1 {
    font-size: 2.2rem;
  }

  .heroPic {
    right: 4vw;
  }

  .btns {
    gap: 1rem;
  }

  .btns button {
    width: 110px;
  }
}
